<template>
  <div class="addBusiness" :class="$root.themeHomeChange === '1' ? 'addBusinessL' : ''">
    <div class="nav">
      <el-button type="primary" size="mini" @click="openAdd">新增</el-button>
      <div style="display: inline-block;float: right;margin-right: 10px">
        <bda-input
            style="width: 200px;margin-right: 4px"
            v-model="keyWords"
            clearable
            placeholder="请输入关键字查询"
        >
        </bda-input>
        <bda-button @click="getDatas">查询</bda-button>
      </div>
    </div>
    <div style="height: calc(100% - 36px)">
      <element-bda-table-page
          v-loading="dsLoading"
          element-loading-text="拼命加载中"
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(0, 0, 0, 0.8)"
          :tableData="majorArray"
          :columnArray="majorCol"
          :pageSize = pagesRs.pageSize
          :pageNo = pagesRs.pageNo
          :total = pagesRs.total
          @getRow="getMajorRow"
          @getPageNo="changePageNo"
          @getPageSize="changePageSize">
      </element-bda-table-page>
    </div>
    <bda-dialog
        :visible="dialogVisible"
        :modal="false"
        :close-on-click-modal="false"
        @close="closeDialog"
        @cancel="closeDialog"
        title="废改立计划值新增"
    >
      <div class="dialogBox" >
        <el-form :model="dynamicValidateForm" :rules="{}" ref="dynamicValidateForm" label-width="160px" class="demo-dynamic">
          <el-form-item label="业务部门" prop="department" :rules="{required: true, message: '阶段名称不能为空', trigger: 'blur'}">
            <el-select v-model="dynamicValidateForm.department" placeholder="请选择部门" class="inputJd" @change="departmentChange">
              <el-option v-for="item in departmentArray"
                         :label="item.name"
                         :value="item.code"
                         :key="item.code">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="类型" prop="typeVal" :rules="{required: true, message: '阶段名称不能为空', trigger: 'blur'}">
            <el-select v-model="dynamicValidateForm.typeVal" placeholder="类型" class="inputJd">
              <el-option label="废止" value="废止"></el-option>
              <el-option label="修订与新增" value="修订与新增"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="年份" prop="yearData" :rules="{required: true, message: '年份不能为空', trigger: 'blur'}">
            <el-date-picker
                :value-format="'yyyy'"
                v-model="dynamicValidateForm.yearData"
                type="year"
                class="inputJd"
                placeholder="选择年">
            </el-date-picker>
          </el-form-item>
          <el-form-item
              prop="domain"
              :label="'计划值'"
              :rules="{required: true, message: '计划值不能为空', trigger: 'blur'}"
          >
            <bda-input class="inputJd" v-model="dynamicValidateForm.domain" placeholder="请输入计划值"></bda-input>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
      <bda-button bdatype="primary" @click="submitForm('dynamicValidateForm')">保存</bda-button>
      <el-button @click="resetForm('dynamicValidateForm')">重置</el-button>
       <bda-button bdatype="defualt" @click="closeDialog">关闭</bda-button>
    </span>
    </bda-dialog>
  </div>
</template>

<script>
  import elementBdaTablePage from '@/components/table/element-bda-table-page'
  import Interface from "../../../../../base/mixin/enterprise-manage-center";
export default {
  name:'addBusiness',
  mixins: [Interface],
  components:{
    elementBdaTablePage
  },
  props: [],
  data() {
    return {
      majorArray: [],
      majorCol:[],
      dsLoading:false,
      pagesRs:{
        pageNo: 1,
        pageSize: 10,
        total: 0,
      },

      //弹窗编辑
      operatType:'新增',


      //弹窗新增
      dialogVisible:false,
      keyWords:'',
      rowId:'',
      departmentArray:[],
      dynamicValidateForm: {
        department:'',
        departmentName:'',
        typeVal:'',
        yearData:'',
        domain:'',
      }
    };
  },
  mounted() {
    this.getDatas()
  },
  created() {
    this.getDepartment()
  },
  watch: {

  },
  methods: {
    //所属部门
    getDepartment(){
      this.department({}).then((res)=>{
        this.departmentArray = res||[]
      })
    },
    departmentChange(v){
      for (let i =0;i<this.departmentArray.length;i++){
        if (v===this.departmentArray[i].code){
          this.dynamicValidateForm.departmentName = this.departmentArray[i].name
          return
        }
      }
    },

    changePageNo(pageNo){
      this.pagesRs.pageNo = pageNo
      this.getDatas()
    },
    changePageSize(size){
      this.pagesRs.pageSize = size
      this.getDatas()
    },
    getMajorRow(v,op){
      if (op==='删除'){
        this.deleteData(v)
      }else if (op==='编辑'){
        this.operatType = op
        this.dialogVisible = true
        this.rowId = v.id||''
        this.GetPhaseDetail(v)
      }
    },
    //删除
    deleteData(row){
      this.fglDelete({
        id :row.id
      }).then(()=>{
        this.getDatas()
      })
    },
    //查询详情
    GetPhaseDetail(row){
      this.fglGet({
        id:row.id
      }).then((res)=>{
        if (res){
          this.dynamicValidateForm.departmentName = res.department||""
          this.dynamicValidateForm.department = res.departmentId||""
          this.dynamicValidateForm.typeVal = res.type||""
          this.dynamicValidateForm.yearData = res.year||""
          this.dynamicValidateForm.domain = res.planValue||""
        }
      })
    },
    //编辑
    editData(){
      let UpdateBusinessDTO = {
        department:this.dynamicValidateForm.departmentName,
        departmentId:this.dynamicValidateForm.department,
        type:this.dynamicValidateForm.typeVal,
        year:this.dynamicValidateForm.yearData,
        planValue:this.dynamicValidateForm.domain,
        id:this.rowId
      }
      this.fglUpdate(UpdateBusinessDTO).then(()=>{
        this.getDatas()
      }).finally(()=>{
        this.dialogVisible = false
      })

    },
    //新增
    openAdd(){
      this.operatType = '新增'
      this.dialogVisible = true
    },
    addSave(){
      this.fglAdd({
        department:this.dynamicValidateForm.departmentName,
        departmentId:this.dynamicValidateForm.department,
        type:this.dynamicValidateForm.typeVal,
        year:this.dynamicValidateForm.yearData,
        planValue:this.dynamicValidateForm.domain,
      }).then(()=>{
        this.getDatas()
      }).finally(()=>{
        this.dialogVisible = false
      })
    },

    //查询列表
    getDatas(){
      this.dsLoading = true
      this.majorCol=[
        {prop: 'planValue', label: '计划值'},
        {prop: 'type', label: '类型'},
        {prop: 'department', label: '部门'},
        {prop: 'createName', label: '创建人',},
        {prop: 'createTime', label: '创建时间',},
        {prop: 'updateTime', label: '更新时间'},
        {type: 'operate', linkName: ['编辑','删除'], label: '操作', width: '180'}]
      this.fglList({
        current:this.pagesRs.pageNo,
        size:this.pagesRs.pageSize,
        "keyword": this.keyWords,
      }).then((res)=>{
        this.pagesRs.total = res.records
        this.majorArray = res.rows||[]
      }).finally(()=>{
        this.dsLoading = false
      })
    },

    /**
     * @description:关闭弹框回调
     */
    closeDialog() {
      this.dialogVisible = false;
      this.resetForm('dynamicValidateForm')
    },
    //新增
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid&this.operatType==='新增') {
          this.addSave()
        } else  if (valid&this.operatType==='编辑') {
          this.editData()
        }else {
          return false;
        }
      });
    },
    //编辑


    resetForm(formName) {
      this.$refs[formName].resetFields();
    },

  },
};
</script>

<style lang="scss" scoped>
.addBusiness {
  width: 100%;
  height: 100%;
  padding: 0 10px;
  box-sizing: border-box;
  color: #ffffff;
  .nav{
    height: 30px;
    line-height: 30px;
    margin-bottom: 6px;
  }
  /deep/ {
    /*修改添加规则弹窗 */
    .el-dialog {
      width: 1094px;
      height: 540px;
      background: #1f2c4d;
      border: 1px solid #4389f9;
      border-radius: 4px;

      .el-dialog__header {
        border-bottom: 1px solid #4389f9 !important;
        height: 48px !important;

        .bda-dialog-header .bda-dialog-header-title {
          font-family: MicrosoftYaHei-Bold;
          font-size: 16px !important;
          color: #ffffff !important;
          line-height: 48px;
          font-weight: 700;
        }
      }

      .dialogBox{
        color: #ffffff;
        .jdName{
          width: 160px;
          display: inline-block;
          text-align: right;
          box-sizing: border-box;
          padding-right: 20px;
        }
        .inputJd{
          width: calc(100% - 240px);
        }
        .jdBtn{
          width: 80px;
        }
      }
      .bda-dialog-body-main {
        height: 438px !important;

        .el-form {
          .el-form-item__label{
            color: #ffffff;
          }
          .el-row:nth-of-type(3) {
            height: 80px;
            .el-textarea__inner {
              height: 60px;
              background: #041338;
              border: 1px solid #4389f9;
              color: #4298f3;
              &::placeholder {
                font-family: MicrosoftYaHei;
                font-size: 14px;
                color: #4298f3;
                font-weight: 400;
              }
            }
          }

          .el-row:nth-of-type(4) {
            height: 110px;
            .el-textarea__inner {
              height: 90px;
              background: #041338;
              border: 1px solid #4389f9;
              color: #4298f3;
              &::placeholder {
                font-family: MicrosoftYaHei;
                font-size: 14px;
                color: #4298f3;
                font-weight: 400;
              }
            }
          }
        }
      }

      .el-dialog__footer {
        border-top: 1px solid #4389f9 !important;
        height: 52px !important;

        .dialog-footer {
          display: block;
          height: 100%;
          padding: 11px 20px;
          box-sizing: border-box;
        }
      }
    }
  }
}
.addBusinessL{
  //弹窗样式
  ::v-deep .el-dialog {
    background: #ffffff!important;
    border: 1px solid #c2ddff;
    border-radius: 4px;
    .bda-dialog-body-main {
      height: 438px !important;

      .el-form {
        .el-form-item__label{
          color: #333333;
        }
        .el-row:nth-of-type(3) {
          height: 80px;
          .el-textarea__inner {
            height: 60px;
            background: #041338;
            border: 1px solid #4389f9;
            color: #4298f3;
            &::placeholder {
              font-family: MicrosoftYaHei;
              font-size: 14px;
              color: #4298f3;
              font-weight: 400;
            }
          }
        }

        .el-row:nth-of-type(4) {
          height: 110px;
          .el-textarea__inner {
            height: 90px;
            background: #041338;
            border: 1px solid #4389f9;
            color: #4298f3;
            &::placeholder {
              font-family: MicrosoftYaHei;
              font-size: 14px;
              color: #4298f3;
              font-weight: 400;
            }
          }
        }
      }
    }
    .el-dialog__header {
      border-bottom: 1px solid #cccccc !important;
      height: 48px !important;

      .bda-dialog-header .bda-dialog-header-title {
        font-family: MicrosoftYaHei-Bold;
        font-size: 16px !important;
        color: #333333 !important;
        line-height: 48px;
        font-weight: 700;
      }
    }
    .el-dialog__footer {
      border-top: 1px solid #cccccc !important;
      height: 52px !important;

      .dialog-footer {
        display: block;
        height: 100%;
        padding: 11px 20px;
        box-sizing: border-box;
      }
    }
  }
}
</style>